import dynamic from "next/dynamic";

const HelloBundle = dynamic({
  modules: () => {
    const components = {
      Hello1a: () => import("../components/dynamic/hello1"),
      Hello2a: () => import("../components/dynamic/hello2")
    };

    return components;
  },
  render: components => (
    <div>
      <Hello1a />
      <Hello2a />
    </div>
  )
});

function DynamicImport() {
  return <HelloBundle title="Dynamic Bundle" />;
}

export default DynamicImport;
